import * as React from 'react';
import Button from '@mui/material/Button';
import ClickAwayListener from '@mui/material/ClickAwayListener';
import Grow from '@mui/material/Grow';
import Paper from '@mui/material/Paper';
import Popper from '@mui/material/Popper';
import MenuItem from '@mui/material/MenuItem';
import MenuList from '@mui/material/MenuList';
import Stack from '@mui/material/Stack';
// import { ArrowDropDownOutlinedIcon } from '@mui/icons-material';
import ArrowDropDownOutlinedIcon from '@mui/icons-material/ArrowDropDownOutlined';
// import NexaLight from '../../../assets/fonts/Nexa-Light.otf'
import { Link } from "react-router-dom";
export default function MenuListComposition() {
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);

    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };

    const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }

        setOpen(false);
    };

    function handleListKeyDown(event) {
        if (event.key === 'Tab') {
            event.preventDefault();
            setOpen(false);
        } else if (event.key === 'Escape') {
            setOpen(false);
        }
    }

    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
        if (prevOpen.current === true && open === false) {
            anchorRef.current.focus();
        }

        prevOpen.current = open;
    }, [open]);

    return (
        <Stack direction="row" spacing={2}>
            {/* <Paper>
                <MenuList>
                    <MenuItem>Profile</MenuItem>
                    <MenuItem>My account</MenuItem>
                    <MenuItem>Logout</MenuItem>
                </MenuList>
            </Paper> */}
            <div>
                <Button
                    ref={anchorRef}
                    id="composition-button"
                    aria-controls={open ? 'composition-menu' : undefined}
                    aria-expanded={open ? 'true' : undefined}
                    aria-haspopup="true"
                    onClick={handleToggle}
                    sx={{ color: '#525659', paddingLeft: '0', paddingRight: '0', fontFamily: 'nexa-light', fontSize: '0.92rem', letterSpacing: '0.1rem', textTransform: 'none' }}
                >
                    More
                    <ArrowDropDownOutlinedIcon style={{
                        left: '-4px',
                        bottom: '0.5px',
                        position: 'relative'
                    }} />
                </Button>
                <Popper
                    open={open}
                    anchorEl={anchorRef.current}
                    role={undefined}
                    placement="bottom-start"
                    transition
                    disablePortal
                >
                    {({ TransitionProps, placement }) => (
                        <Grow
                            {...TransitionProps}
                            style={{
                                transformOrigin:
                                    placement === 'bottom-start' ? 'left top' : 'left bottom',
                            }}
                        >
                            <Paper style={{ boxShadow: '2px 6px 1px -2px rgba(0,0,0,0.75)',webkitBoxShadow: '2px 6px 21px -2px rgba(0,0,0,0.75)',MozBoxShadow: '2px 6px 21px -2px rgba(0,0,0,0.75)'}}>
                                <ClickAwayListener onClickAway={handleClose}>
                                    <MenuList
                                        autoFocusItem={open}
                                        id="composition-menu"
                                        aria-labelledby="composition-button"
                                        onKeyDown={handleListKeyDown}
                                        sx={{ display: 'flex', flexDirection: 'column', margin: '0 0px 0 22px', color: '#525659', paddingLeft: '10px', paddingRight: '5px'  }}
                                    >
                                        <MenuItem sx={{ fontFamily: 'nexa-light', fontSize: '0.92rem', letterSpacing: '0.1rem' }} onClick={handleClose}>Tvastra Plus</MenuItem>
                                        <MenuItem sx={{ fontFamily: 'nexa-light', fontSize: '0.92rem', letterSpacing: '0.1rem', marginTop:'3px' }} onClick={handleClose}>Dentirstry</MenuItem>
                                        <MenuItem sx={{ fontFamily: 'nexa-light', fontSize: '0.92rem', letterSpacing: '0.1rem', marginTop: '3px' }} onClick={handleClose}><Link to='/appointment'>Appointment</Link></MenuItem>
                                        <MenuItem sx={{ fontFamily: 'nexa-light', fontSize: '0.92rem', letterSpacing: '0.1rem', marginTop: '3px', MarginBottom: '3px' }} onClick={handleClose}>Logout</MenuItem>
                                    </MenuList>
                                </ClickAwayListener>
                            </Paper>
                        </Grow>
                    )}
                </Popper>
            </div>
        </Stack>
    );
}